let array = [];
axios
  .get("http://45.127.133.75:3000/api/iems/list-periode")
  .then((res) => {
    const tempData = res.data;
    tempData.forEach((item) => {
      array.push({
        label: item.PERIODE,
        value: item.PERIODE,
      });
    });
    array.reverse();
  })
  .catch((err) => {
    console.log(err);
  });
export default array;
