<template>
  <div>
    <div class="rkl-matriks">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <div v-if="$route.params.render == 'daftar'" class="content">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <el-select
                v-show="$store.getters.isAdmin"
                v-model="selects.unitId"
                size="mini"
                placeholder="Pilih Unit Kerja"
                class="mr-2"
                @change="getData(selects.unitId)"
              >
                <el-option
                  v-for="option in cabang"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <el-select
                v-model="periode"
                size="mini"
                placeholder="Pilih Periode"
                class="mr-2"
                @change="changePeriode(periode)"
              >
                <el-option
                  v-for="option in listPeriode"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <base-button size="sm" type="neutral" @click="showForm"
                >Tambah</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div>
            <div class="card">
              <div class="card-header">
                <h2 class="m-0">
                  Daftar Matriks Rencana Pengelolaan Lingkungan
                </h2>
              </div>
              <div>
                <el-table
                  :data="queriedData"
                  row-key="id"
                  header-row-class-name="thead-light"
                  @sort-change="sortChange"
                  @row-click="toDetail"
                >
                  <el-table-column
                    sortable
                    v-for="column in tableColumns"
                    :key="column.label"
                    v-bind="column"
                  ></el-table-column>
                </el-table>
              </div>
              <div
                slot="footer"
                class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
              >
                <div class="d-flex align-items-center">
                  <p class="card-category mb-0 mr-2">Menampilkan</p>
                  <div style="width: 80px">
                    <el-select
                      class="select-primary pagination-select"
                      v-model="pagination.perPage"
                      placeholder="Per page"
                      size="mini"
                    >
                      <el-option
                        class="select-primary"
                        v-for="item in pagination.perPageOptions"
                        :key="item"
                        :label="item"
                        :value="item"
                      ></el-option>
                    </el-select>
                  </div>
                  <p class="card-category m-0 ml-2">
                    Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                    <span v-if="selectedRows.length"
                      >&nbsp; &nbsp; {{ selectedRows.length }} rows
                      selected</span
                    >
                  </p>
                </div>
                <base-pagination
                  class="pagination-no-border my-3"
                  v-model="pagination.currentPage"
                  :per-page="pagination.perPage"
                  :total="total"
                ></base-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>
      <template v-if="$route.params.render == 'form'">
        <base-header class="pt-4 pb-6" type="primary"></base-header>
        <div class="container-fluid mt--6">
          <div class="card border-0">
            <div class="card-header">
              <h2 class="m-0">Form Matriks Rencana Pengelolaan Lingkungan</h2>
            </div>
            <div class="card-body">
              <base-input
                v-show="$store.getters.isAdmin"
                label="Unit Kerja"
                class="form-group"
              >
                <select
                  v-model="uk_id"
                  @change="checkILID(uk_id)"
                  class="form-control"
                >
                  <option
                    v-for="option in cabang"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></option>
                </select>
              </base-input>
              <base-input label="Periode" class="form-group">
                <select
                  v-model="formMatriksRKL.mrkl_periode"
                  class="form-control"
                >
                  <option
                    v-for="option in listPeriode"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></option>
                </select>
              </base-input>
              <base-input label="Izin Lingkungan" class="form-group">
                <select v-model="formMatriksRKL.il_id" class="form-control">
                  <option
                    v-for="option in listIL"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></option>
                </select>
              </base-input>
              <base-input label="Tahapan" class="form-group">
                <select
                  v-model="formMatriksRKL.mrkl_tahapan"
                  class="form-control"
                >
                  <option>Pra-Konstruksi</option>
                  <option>Konstruksi</option>
                  <option>Operasi</option>
                  <option>Paska Oprasi</option>
                </select>
              </base-input>
              <base-input label="Kegiatan" class="form-group">
                <textarea
                  v-model="formMatriksRKL.mrkl_kegiatan"
                  class="form-control b-form-textarea"
                  rows="1"
                  resize="none"
                ></textarea>
              </base-input>
              <base-input label="Sumber Dampak" class="form-group">
                <textarea
                  v-model="formMatriksRKL.mrkl_sumberdampak"
                  class="form-control b-form-textarea"
                  rows="5"
                  resize="none"
                ></textarea>
              </base-input>
              <base-input label="Jenis Limbah/Cemaran" class="form-group">
                <textarea
                  v-model="formMatriksRKL.mrkl_jenlimbah"
                  class="form-control b-form-textarea"
                  rows="1"
                  resize="none"
                ></textarea>
              </base-input>

              <label class="form-control-label">Besaran Dampak</label>
              <form>
                <html-editor
                  class="force-inherit"
                  v-model="formMatriksRKL.mrkl_besdampak"
                ></html-editor>
              </form>
              <!-- <button @click="ngonsol"></button> -->
              <label class="form-control-label">Tolak Ukur Dampak</label>
              <form>
                <html-editor
                  v-model="formMatriksRKL.mrkl_tolukur"
                ></html-editor>
              </form>
              <label class="form-control-label">Tujuan</label>
              <form>
                <html-editor v-model="formMatriksRKL.mrkl_tujuan"></html-editor>
              </form>
              <div class="row mb-2">
                <div class="col-6">
                  <div class="row align-items-center">
                    <div class="col">
                      <label class="form-control-label m-0"
                        >Upaya Pengelolaan Lingkungan</label
                      >
                    </div>
                  </div>
                </div>
                <div class="col-6">
                  <div class="row align-items-center">
                    <div class="col">
                      <label class="form-control-label m-0 text-right"
                        >Jumlah Upaya</label
                      >
                    </div>
                    <div class="col-2 mb--4">
                      <base-input v-model="jumlahUpaya" />
                    </div>
                  </div>
                </div>
              </div>
              <form>
                <html-editor
                  v-model="formMatriksRKL.mrkl_upengelolaan[index - 1]"
                  v-for="index in nullHandler(jumlahUpaya)"
                  :key="index"
                ></html-editor>
              </form>
              <label class="form-control-label">Lokasi</label>
              <form>
                <html-editor v-model="formMatriksRKL.mrkl_loc"></html-editor>
              </form>
              <label class="form-control-label">Pengawas</label>
              <form>
                <html-editor
                  v-model="formMatriksRKL.mrkl_pengawas"
                ></html-editor>
              </form>
              <label class="form-control-label">Pelaksana</label>
              <form>
                <html-editor
                  v-model="formMatriksRKL.mrkl_pelaksana"
                ></html-editor>
              </form>
              <label class="form-control-label">Pelaporan</label>
              <form>
                <html-editor
                  v-model="formMatriksRKL.mrkl_pelaporan"
                ></html-editor>
              </form>

              <div class="col-lg-12 col-5 px-0 text-right">
                <base-button size="md" type="danger" @click="hideForm"
                  >Kembali</base-button
                >
                <base-button
                  v-if="!isUpdate"
                  size="md"
                  type="primary"
                  @click="submitForm"
                  >Simpan</base-button
                >
                <base-button
                  v-if="isUpdate"
                  size="md"
                  type="primary"
                  @click="submitFormUpdate"
                  >Update</base-button
                >
              </div>
            </div>
          </div>
        </div>
      </template>
      <div v-if="$route.params.render == 'detail'">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <base-button
                @click="toUpdateForm"
                class="ml-2"
                size="sm"
                type="neutral"
                >Ubah</base-button
              >
              <base-button
                size="sm"
                type="danger"
                @click="deleteData(tempEditData.MATRIKSRKL_ID)"
                >Hapus</base-button
              >
            </div>
          </div>
        </base-header>
        <div class="container-fluid mt--6">
          <div class="card">
            <div class="card-header">
              <h2 class="m-0">{{ detailMatriks.title }}</h2>
            </div>
            <div class="card-body">
              <template v-for="(body, index) in detailMatriks.body">
                <div class="row">
                  <div class="col-2">
                    <span class="m-0">
                      <b>{{ body.judul }}</b>
                    </span>
                  </div>
                  <p v-if="isText(body.type)" class="col-10">{{ body.isi }}</p>
                  <span
                    v-if="isHTML(body.type)"
                    class="col-10"
                    v-html="body.isi"
                  ></span>
                  <div class="col-10">
                    <span
                      v-if="isMany(body.type)"
                      class="col-10"
                      v-for="isian in body.isi"
                      v-html="isian"
                    ></span>
                  </div>
                  <hr
                    v-if="index < detailMatriks.body.length - 1"
                    class="w-100 mb-3"
                  />
                </div>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
import axios from "axios";
import cabang from "../../cabang";
import listIL from "@/views/Pages/izinLingkunganList.js";
import listPeriode from "@/views/Pages/listPeriode.js";

export default {
  mixins: [clientPaginationMixin],
  components: {
    HtmlEditor,
    BasePagination,
    BaseInput,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      listIL,
      periode: "",
      listPeriode,
      matriksrkl_id: 0,
      temp: {},
      testModel: "",
      cabang,
      selects: {
        unitId: null
      },
      jumlahUpaya: 1,
      uk_id: null,
      formMatriksRKL: {
        periode: "",
        il_id: 0,
        mrkl_tahapan: "",
        mrkl_kegiatan: "",
        mrkl_sumberdampak: "",
        mrkl_jenlimbah: "",
        mrkl_besdampak: "",
        mrkl_tolukur: "",
        mrkl_tujuan: "",
        mrkl_upengelolaan: [],
        mrkl_loc: "",
        mrkl_periode: "",
        mrkl_pelaksana: "",
        mrkl_pengawas: "",
        mrkl_pelaporan: ""
      },
      detailMatriks: {},
      dates: {
        simple: new Date("2020-10-10"),
        range: "2019-04-19 to 2019-04-24"
      },
      isTable: true,
      isForm: false,
      isDetail: false,
      isUpdate: false,
      tableColumns: [
        {
          align: "left",
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 150
        },
        {
          prop: "MRKL_PERIODE",
          label: "Periode",
          minWidth: 150
        },
        {
          prop: "IL_NAME",
          label: "Izin Lingkungan",
          minWidth: 150
        },
        {
          prop: "MRKL_KEGIATAN",
          label: "KEGIATAN",
          minWidth: 150
        },
        {
          prop: "MRKL_SUMBERDAMPAK",
          label: "Sumber Dampak",
          minWidth: 150
        }
      ],
      tableData: [],
      selectedRows: []
    };
  },
  created() {
    this.insertUKID();
    // this.getListPeriode();
  },
  // watch: {
  //   jumlahUpaya(){
  //     this.jumlahUpaya.type != Number && this.jumlahUpaya < 1 ? this.jumlahUpaya == 1 : ""
  //   }
  // },
  methods: {
    changePeriode(value) {},
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then(res => {
          console.log(res.data);
          let tempData = [];
          res.data.forEach(item => {
            tempData.push({
              name: item.PERIODE,
              value: item.PERIODE
            });
          });
          console.log(tempData);
          this.listPeriode = tempData;
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteData(id) {
      swal
        .fire({
          title: "Apakah anda yakin untuk menghapus?",
          showCancelButton: true,
          confirmButtonText: `Hapus`,
          cancelButtonText: `Kembali`,
          confirmButtonColor: "#d33",
          cancelButtonColor: "#3085d6",
          icon: "question"
        })
        .then(result => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios
              .delete("http://45.127.133.75:3000/api/iems/mrkl/" + id)
              .then(res => {
                swal
                  .fire({
                    title: `Success`,
                    text: "Berhasil terhapus!",
                    buttonsStyling: false,
                    confirmButtonClass: "btn btn-success",
                    icon: "success"
                  })
                  .then(result => {
                    this.insertUKID();
                    this.$router.push("daftar");
                  });
              })
              .catch(err => {
                console.log(err);
                this.swalError("Terjadi Kesalahan " + err);
              });
          }
        });
    },
    insertUKID() {
      // this.uk_id = this.$store.getters.getUKId;
      this.getData(this.$store.getters.getUKId);
      if (!this.$store.getters.isAdmin) {
        this.uk_id = this.$store.getters.getUKId;
        this.getILID(this.$store.getters.getUKId);
        this.getData(9999);
      }
    },
    swalError(message) {
      swal.fire({
        title: `Error`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-danger",
        icon: "error"
      });
    },
    swalWarning(message) {
      swal.fire({
        title: `Warning`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-warning",
        icon: "warning"
      });
    },
    toUpdateForm() {
      this.isUpdate = true;
      this.uk_id = this.tempEditData.UNITKERJA_ID;
      this.getILID(this.uk_id);
      this.matriksrkl_id = this.tempEditData.MATRIKSRKL_ID;
      this.formMatriksRKL.il_id = this.tempEditData.IL_ID;
      this.formMatriksRKL.mrkl_besdampak = this.tempEditData.MRKL_BESDAMPAK;
      this.formMatriksRKL.mrkl_jenlimbah = this.tempEditData.MRKL_JENLIMBAH;
      this.formMatriksRKL.mrkl_kegiatan = this.tempEditData.MRKL_KEGIATAN;
      this.formMatriksRKL.mrkl_loc = this.tempEditData.MRKL_LOC;
      this.formMatriksRKL.mrkl_pelaksana = this.tempEditData.MRKL_PELAKSANA;
      this.formMatriksRKL.mrkl_pelaporan = this.tempEditData.MRKL_PELAPORAN;
      this.formMatriksRKL.mrkl_pengawas = this.tempEditData.MRKL_PENGAWAS;
      this.formMatriksRKL.mrkl_periode = this.tempEditData.MRKL_PERIODE;
      this.formMatriksRKL.mrkl_sumberdampak = this.tempEditData.MRKL_SUMBERDAMPAK;
      this.formMatriksRKL.mrkl_tahapan = this.tempEditData.MRKL_TAHAPAN;
      this.formMatriksRKL.mrkl_tolukur = this.tempEditData.MRKL_TOLUKUR;
      this.formMatriksRKL.mrkl_tujuan = this.tempEditData.MRKL_TUJUAN;
      let tempUpdateUP = JSON.parse(this.tempEditData.MRKL_UPENGELOLAAN);

      this.jumlahUpaya = tempUpdateUP.length;
      console.log(this.jumlahUpaya + "jumllah upaya");
      for (let i = 0; i < this.jumlahUpaya; i++) {
        this.formMatriksRKL.mrkl_upengelolaan[i] = tempUpdateUP[i];
      }
      this.$router.push("form");
    },

    isHTML(data) {
      return data === "HTML";
    },
    isText(data) {
      return data === "text";
    },
    isMany(data) {
      return data === "many";
    },
    isNotMany(data) {
      return data !== "many";
    },
    toDetail(row, col, event) {
      this.tempEditData = row;

      this.isTable = false;
      this.isDetail = true;
      let tempUpengelolaan = row.MRKL_UPENGELOLAAN;
      tempUpengelolaan = JSON.parse(tempUpengelolaan);

      for (let i = 0; i < tempUpengelolaan.length - 1; i++) {
        tempUpengelolaan[i] += "<hr class='w-100 my-2' />";
      }
      let namaIzinLingkungan = listIL.filter(item => item.value == row.IL_ID);

      let dataDetail = {
        title: row.MRKL_KEGIATAN,
        body: [
          { judul: "Tahapan", isi: row.MRKL_TAHAPAN, type: "text" },
          {
            judul: "Periode",
            isi: row.MRKL_PERIODE,
            type: "text"
          },
          {
            judul: "Izin Lingkungan",
            isi: namaIzinLingkungan[0].label,
            type: "text"
          },
          {
            judul: "Kegiatan",
            isi: row.MRKL_KEGIATAN,
            type: "text"
          },
          {
            judul: "Sumber Dampak",
            isi: row.MRKL_SUMBERDAMPAK,
            type: "text"
          },
          {
            judul: "Jenis Limbah / Cemaran",
            isi: row.MRKL_JENLIMBAH,
            type: "text"
          },
          { judul: "Besaran Dampak", isi: row.MRKL_BESDAMPAK, type: "HTML" },
          { judul: "Tolak Ukur Dampak", isi: row.MRKL_TOLUKUR, type: "HTML" },
          {
            judul: "Upaya Pengelolaan Lingkungan",
            isi: tempUpengelolaan,
            type: "many"
          },
          {
            judul: "Lokasi",
            isi: row.MRKL_LOC,
            type: "HTML"
          },
          {
            judul: "Pelaksana",
            isi: row.MRKL_PELAKSANA,
            type: "HTML"
          },
          {
            judul: "Pengawas",
            isi: row.MRKL_PENGAWAS,
            type: "HTML"
          },
          {
            judul: "Pelaporan",
            isi: row.MRKL_PELAPORAN,
            type: "HTML"
          }
        ]
      };

      this.detailMatriks = dataDetail;
      this.$router.push("detail");
    },
    getILID(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + data)
        .then(res => {
          this.listIL = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    checkILID(UKID) {
      function compare(a, b) {
        // Use toUpperCase() to ignore character casing
        const nameA = a.label.toUpperCase();
        const nameB = b.label.toUpperCase();

        let comparison = 0;
        if (nameA > nameB) {
          comparison = 1;
        } else if (nameA < nameB) {
          comparison = -1;
        }
        return comparison;
      }
      let array = [];
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + UKID)
        .then(res => {
          const tempData = res.data;
          tempData.forEach(item => {
            array.push({
              label: item.IL_NAME,
              value: item.IL_ID
            });
          });
          array.sort(compare);
          if (res.data) {
            this.listIL = array;
          } else {
            swal
              .fire({
                title: `Unit Kerja ini belum memiliki Izin Lingungan`,
                text: "Silakan isi Izin Lingungan terlebih dahulu!",
                buttonsStyling: false,
                showCancelButton: true,
                confirmButtonText: "Ke Form Izin Lingkungan",
                cancelButtonText: "Kembali",
                confirmButtonClass: "btn btn-success",
                cancelButtonClass: "btn btn-danger",
                icon: "error"
              })
              .then(result => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {
                  this.$router.push("/iems/izin-lingkungan/form");
                }
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getData(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/rkl/" + data)
        .then(res => {
          console.log(res.data);
          let tempTable = res.data;
          this.tableData = tempTable;
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitFormUpdate() {
      let updateData = this.formMatriksRKL;
      console.log(updateData);
      updateData.mrkl_upengelolaan = JSON.stringify(
        updateData.mrkl_upengelolaan
      );

      axios
        .put(
          "http://45.127.133.75:3000/api/iems/rkl-matriks/" +
            this.matriksrkl_id,
          updateData
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Update Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.insertUKID();
              this.$router.push("daftar");
              this.isUpdate = false;
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    submitForm() {
      let formData = this.formMatriksRKL;
      if (formData.il_id == 0) {
        swal.fire({
          title: `Warning`,
          text: "Pilih Unit Kerja terlebih dahulu!!!",
          buttonsStyling: false,
          confirmButtonClass: "btn btn-warning",
          icon: "warning"
        });
        return;
      }
      formData.mrkl_upengelolaan = JSON.stringify(
        this.formMatriksRKL.mrkl_upengelolaan
      );
      axios
        .post("http://45.127.133.75:3000/api/iems/rkl-new", formData)
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              const originForm = {
                il_id: "",
                mrkl_tahapan: "",
                mrkl_kegiatan: "",
                mrkl_sumberdampak: "",
                mrkl_jenlimbah: "",
                mrkl_besdampak: "",
                mrkl_tolukur: "",
                mrkl_tujuan: "",
                mrkl_upengelolaan: [],
                mrkl_loc: "",
                mrkl_periode: "",
                mrkl_pelaksana: "",
                mrkl_pengawas: "",
                mrkl_pelaporan: ""
              };
              this.formMatriksRKL = originForm;
              this.insertUKID();
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    nullHandler(n) {
      if (!n) {
        return 1;
      }
      return parseInt(n);
    },
    showDetail() {
      this.isForm = false;
      this.isTable = false;
      this.isDetail = true;
    },
    showForm() {
      this.getILID(this.$store.getters.getUKId);
      if (this.formMatriksRKL.il_id == 0 && !this.$store.getters.isAdmin) {
        swal
          .fire({
            title: `Unit Kerja ini belum memiliki Izin Lingungan`,
            text: "Silakan isi Izin Lingungan terlebih dahulu!",
            buttonsStyling: false,
            showCancelButton: true,
            confirmButtonText: "Ke Form Izin Lingkungan",
            cancelButtonText: "Kembali",
            confirmButtonClass: "btn btn-success",
            cancelButtonClass: "btn btn-danger",
            icon: "error"
          })
          .then(result => {
            /* Read more about isConfirmed, isDenied below */
            if (result.isConfirmed) {
              this.$router.push("/iems/izin-lingkungan/form");
            }
          });
        return;
      }
      this.isUpdate = false;
      this.$router.push("form");
    },
    hideForm() {
      this.$router.push("daftar");
      if (this.$store.getters.isAdmin) {
        this.uk_id = null;
        this.getData(9999);
      }
    }
  }
};
</script>
