import axios from "axios";

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const nameA = a.label.toUpperCase();
  const nameB = b.label.toUpperCase();

  let comparison = 0;
  if (nameA > nameB) {
    comparison = 1;
  } else if (nameA < nameB) {
    comparison = -1;
  }
  return comparison;
}

// let array = [];
let array = [];
var temp = JSON.parse(localStorage.getItem("auth-data"));
let UKID =  temp.idUK
axios
  .get("http://45.127.133.75:3000/api/iems/il/"+UKID)
  .then((res) => {
    const tempData = res.data;
    tempData.forEach((item) => {
      array.push({
        label: item.IL_NAME,
        value: item.IL_ID,
      });
    });
    array.sort(compare);
    console.log(array)
  })
  .catch((err) => {
    console.log(err);
  });
export default array;
